#login{
    font-family: sans-serif;
    position: absolute;
    top:40%;
    left:50%;
    transform: translate(-50%,-50%);
    border-radius: 20px;
    width: 60vw;
    max-width: 300px;
    box-shadow: 0 0 15px grey;
}
#login input{
    margin: 32px auto;
    display: block;
    width:60%;
    outline: none;
    border:none;
    border-bottom: 1px solid;
    transition: .2s;
    background: none;
}
#login input:focus{
    border-width: 1.5px;
    border-color: #74b9ff;
}
#login button{
    margin: 15px auto;
    display: block;
    border: 0;
    padding: 15px;
    border-radius: 15px;
    background-color: #fd79a8;
    outline: none;
    color: white;
    cursor: pointer;
    transition: .3s ease-in; 
}
#login button:hover{
    background-color: #e84393;
    transform: scale(1.05);
}
#title{
    display: block;
    text-align: center;
    font-size: 25px;
    padding: 20px;
    font-weight: bold;
}
 
#user{
    position: absolute;
    bottom: 59%;
    left: 20%;
    font-weight: bold;
    transition: .2s ease;
    z-index: -1;
}
#password{
    position: absolute;
    bottom: 42%;
    left: 20%;    
    font-weight: bold;
    transition: .2s ease;
    z-index: -1;
}
#info{
    text-align: center;
    margin: 10px auto;
}
#info a{
    outline: none;
}
#alert{
    color: red;
    position: fixed;
    text-align: center;
    top:70%;
    left:50%;
    transform: translate(-50%,-50%);
    transition: .2s;
}
#loading{
    position: absolute;
    top:80%;
    left:50%;
    transform: translate(-50%,-50%);
}
