img{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
}

#Home{
}
#home-container{
    font-family: monospace;
    font-size: larger;
    position: fixed;
    font-weight: bold;
    top:50%;
    left:50%;
