#register{
    font-family: sans-serif;
    position: absolute;
    top:40%;
    left:50%;
    transform: translate(-50%,-50%);
    border-radius: 20px;
    width: 60vw;
    max-width: 300px;
    box-shadow: 0 0 15px grey;
}
#register input{
    margin: 32px auto;
    display: block;
    width:60%;
    outline: none;
    border:none;
    border-bottom: 1px solid;
    transition: .2s;
    background: none;
}
#register input:focus{
    border-width: 1.5px;
    border-color: #74b9ff;
}
#register button{
    margin: 15px auto;
    display: block;
    border: 0;
    padding: 15px;
    border-radius: 15px;
    background-color: #fd79a8;
    outline: none;
    color: white;
    cursor: pointer;
    transition: .3s ease-in; 
}
#register button:hover{
    background-color: #e84393;
    transform: scale(1.05);
}
#reg-user{
    position: absolute;
    bottom: 59%;
    left: 20%;
    font-weight: bold;
    transition: .2s ease;
    z-index: -1;
}
#reg-password{
    position: absolute;
    bottom: 42%;
    left: 20%;    
    font-weight: bold;
    transition: .2s ease;
    z-index: -1;
}
